import { Component, Prop, h, State, Event, EventEmitter } from '@stencil/core';

/**
 *
 */
@Component({
    tag: 'limel-link-menu',
    shadow: true,
    styleUrl: 'link-menu.scss',
})
export class LinkMenu {
    /**
     * Emitted when the menu is closed from inside the component.
     * (*Not* emitted when the consumer sets the `open`-property to `false`.)
     */
    @Event()
    private close: EventEmitter<void>;
    /**
     * Open state of the dialog
     */
    @Prop()
    public isOpen: boolean = false;

    @State()
    private linkText: string = 'Title';

    @State()
    private linkValue: string = 'Title';

    public render() {
        return [
            <limel-input-field
                label="Text"
                value={this.linkText}
                onChange={this.handleLinkTitleChange}
            />,
            <limel-input-field
                required={true}
                label="Link"
                value={this.linkValue}
                onChange={this.handleLinkValueChange}
            />,
            <div class="actions">
                <limel-button
                    label="Cancel"
                    onClick={this.closeDialog}
                    slot="button"
                />
                <limel-button
                    primary={true}
                    label="Save"
                    disabled={!this.linkValue}
                    onClick={this.submitLink}
                    slot="button"
                />
            </div>,
        ];
    }

    private closeDialog = () => {
        this.close.emit();
    };

    private handleLinkTitleChange = (event: CustomEvent<string>) => {
        this.linkText = event.detail;
    };

    private handleLinkValueChange = (event: CustomEvent<string>) => {
        this.linkValue = event.detail;
    };

    private submitLink() {
        // const command = this.menuCommandFactory.getCommand(value);
        // this.executeCommand(command);
        this.closeDialog();
    }
}
